// Global//

html {
  scroll-behavior: smooth;
}

body {
  height: 100%;
  background-color: rgba(245, 241, 241, 1);
  line-height: 1.5;
  min-width: 300px;
  font-weight: 500;
}

.wrapper {
  max-width: 1200px;
  display: flex;
  margin: 0 auto;
  justify-content: center;
}

.light-shade-color {
  color: #f7f3f0;
}

.serif {
  font-family: "Vidaloka", serif;
}

.sans {
  font-family: "Raleway", sans-serif;
}

.dark-title {
  font-family: "Vidaloka", serif;
  color: #3f3f3f;
  font-size: 6.7vw;
}

.dark-para {
  font-family: "Raleway", sans-serif;
  color: #3f3f3f;
  font-size: 18px;
}

.light-title {
  font-family: "Vidaloka", serif;
  color: #f7f3f0;
  font-size: 6.7vw;
}

.light-para {
  font-family: "Raleway", sans-serif;
  color: #f7f3f0;
  font-size: 18px;
}

.title-head,
.para {
  text-align: center;
  width: 100%;
}

.visually-hidden {
  background-color: #fff;
  color: #000;
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
}

form {
  input[type="email"],
  input[type="submit"],
  input[type="phone"],
  input[type="text"],
  textarea {
    margin: 8px auto;
    width: 90%;
    height: 40px;
    text-decoration: none;
    border-radius: 50px;
    border: none;
    padding-left: 10px;
    font-family: "Raleway", sans-serif;
    font-size: 14px;
  }

  input[type="email"] {
    text-align: left;
    padding-left: 10px;
    background-color: rgba(214, 211, 212, 0.849);
  }

  input[type="submit"] {
    text-align: center;
    background-color: rgba(226, 148, 178, 0.849);
    color: #f7f3f0;
    padding-left: 0px;
    font-size: 16px;
  }

  textarea {
    padding-top: 5px;
    border-radius: 20px;
    height: auto;
  }
}

.main-header,
section {
  padding: 105px 20px;
}

// End of Global //

