@media only screen and (min-width: 768px) {
  .sub-background {
    .sub-banner {
      padding: 90px 0;
      .wrapper {
        display: flex;
        align-items: center;
      }

      .sect-info {
        width: 70%;
        height: auto;
        justify-content: space-evenly;

        .title {
          font-size: 42px;
        }
      }

      .form {
        width: auto;
        form {
          display: flex;
          margin: 10px 15px;
          flex-wrap: wrap;
          min-height: 200px;
          width: auto;

          input[type="email"],
          input[type="submit"] {
            width: 90%;
          }
        }
      }
    }
  }

  .contact-background {
    .contact {
      padding: 60px 0;

      .wrapper {
        align-items: center;
        flex-wrap: nowrap;
      }

      .sect-info {
        .contact-info {
          .contact-wrapper {
            display: flex;
          }
        }
        .title {
          font-size: 42px;
        }
      }

      .form {
        form {
          min-height: 420px;
        }
      }
    }
  }

