@media only screen and (max-width: 800px) {
  //HEADER//

  .menu,
  #menu {
    position: absolute;
    top: 0;
    left: 0;
    display: none;
    flex-wrap: wrap;
    width: 100%;
    background-color: #3d3c3c;
    z-index: 10;
    margin: 0 auto;
    justify-content: center;

    &.open {
      display: flex;
    }

    .header-info {
      padding-top: 20px;
    }

    .nav-mobile {
      display: block;
      width: 100%;
      text-align: center;
      font-size: 18px;
      padding: 10px 0;

      a {
        text-decoration: none;
        list-style: none;
      }
    }

    .icons-mobile {
      display: flex;
      justify-content: center;
      width: 100%;
      height: auto;
      padding-top: 10px;

      svg {
        height: 30px;
        width: 30px;
        margin: 0 10px;
        fill: rgba(226, 148, 178, 0.849);
      }
    }
  }
}
